.icon{
    display: inline-block;
    height: 160px;
    position: relative;
}

.sunny{
    z-index: -1;
    position: absolute;
    top: 22%;
    left: 130%;
    width: 30px;
    height: 30px;
    margin: -1.25em;
    background: #e27500;
    border-radius: 50%;
    box-shadow: 0 0 0 4px #e2a900;
    animation: spin 12s infinite linear;
}
.m-rays {
    position: absolute;
    top: -24px;
    left: 50%;
    display: block;
    width: 6px;
    height: 18px;
    margin-left: -2px;
    background: #e28900;
    border-radius: 0.25em;
    box-shadow: 0 62px #e28900;
}
.m-rays:before,
.m-rays:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 6px;
    height: 18px;
    transform: rotate(60deg);
    transform-origin: 50% 38px;
    background: #e28900;
    border-radius: 0.25em;
    box-shadow: 0 62px #e28900;
}
.m-rays:before {
    transform: rotate(120deg);
}

.sun{
    position: absolute;
    top: 25%;
    left: 25%;
    width: 2.5em;
    height: 2.5em;
    margin: -1.25em;
    background: #e27500;
    border-radius: 50%;
    box-shadow: 0 0 0 0.375em #e2a900;
    animation: spin 12s infinite linear;
}
.rays {
    position: absolute;
    top: -2em;
    left: 50%;
    display: block;
    width: 0.375em;
    height: 1.125em;
    margin-left: -0.1875em;
    background: #e28900;
    border-radius: 0.25em;
    box-shadow: 0 5.375em #e28900;
}
.rays:before,
.rays:after {
    content: '';
    position: absolute;
    top: 0em;
    left: 0em;
    display: block;
    width: 0.375em;
    height: 1.125em;
    transform: rotate(60deg);
    transform-origin: 50% 3.25em;
    background: #e28900;
    border-radius: 0.25em;
    box-shadow: 0 5.375em #e28900;
}
.rays:before {
    transform: rotate(120deg);
}

.cloud{
    height: 60px;
    width: 60px;
    background: #fff;
    border-radius: 50%;
    opacity: .95;
    box-shadow: 22px 13px 0 0px #fff, -3px 15px 0 6px #fff, -30px 12px 0 3px #fff
}

.cloudy{
    position: absolute;
    left: 180%;
    top: 10%;
    z-index: -1;
    display: inline-block;
    height: 18px;
    width: 18px;
    background: #fff;
    border-radius: 50%;
    opacity: .8;
    box-shadow: 10px 10px 0 1.4px #fff,
    0 10px 0 1.4px #fff,
    -12px 8px 0 2.8px #fff;
    animation: clouds 3s infinite linear;
}

