.rain{
    position: absolute;
    top: 40%;
    left: 50%;
    height: 20px;
    width: 20px;
}

.rain:after {
    content: '';
    position: absolute;
    left: 150%;
    top: 100%;
    z-index: 2;
    width: 20px;
    height: 20px;
    background: #4d90fe;
    border-radius: 100% 0 70% 70% / 70% 0 100% 70%;
    transform: rotate(-64deg);
    box-shadow: -2px 30px 0 -1.8px #4d90fe,
    -26px -10px 0 -2px #4d90fe,
    -30px 24px 0 -4px #4d90fe;
    animation: rain 3s linear infinite;
}


.rain:nth-child(2):after{
    left: -200%;
    top: 100%;
}

