.white{
    opacity: 1;
}

@keyframes rain {
    0% {
        background: #4d90fe;
        box-shadow: -2px 30px 0 -1.8px rgba(255,255,255,0.2),
        -26px -10px 0 -2px rgba(255,255,255,0.2),
        -30px 24px 0 -4px #4d90fe;
    }
    25% {
        box-shadow: -2px 30px 0 -1.8px #4d90fe,
        -26px -10px 0 -2px rgba(255,255,255,0.2),
        -30px 24px 0 -4px rgba(255,255,255,0.2);
    }
    50% {
        background: rgba(255,255,255,0.2);
        box-shadow: -2px 30px 0 -1.8px rgba(255,255,255,0.2),
        -26px -10px 0 -2px #4d90fe,
        -30px 24px 0 -4px rgba(255,255,255,0.2);
    }
    100% {
        box-shadow: -2px 30px 0 -1.8px rgba(255,255,255,0.2),
        -26px -10px 0 -2px rgba(255,255,255,0.2),
        -30px 24px 0 -4px #4d90fe;
    }
}

@keyframes lightning{
    45%{
        color: #fff;
        opacity: 0.2;
    }
    50%{
        color: #fff705;
        opacity: 1;
    }
    60%{
        color: #fff;
        opacity: 0.3;
    }
    70%{
        color: #fff;
        opacity: 0.7;
    }
}

@keyframes spin{
    100%{
        transform: rotate(360deg);
    }
}

@keyframes clouds{

    100%{
        left: 20%;
    }
}

